html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Outfit", sans-serif;
  min-height: 100%;
  background-color: #F3F3F5;
}

.swal2-popup {
  font-size: 1.5rem;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.btn-check:checked + .btn, .btn.active, .btn.show, .btn:first-child:active, :not(.btn-check) + .btn:active {
  border: 1px solid #c8c8c8;
}

.alert {
  grid-column: 1/span 3;
}

.top {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .top {
    margin-top: 10rem;
  }
}

.btn-add {
  background-color: rgba(105, 113, 153, 0.1);
  color: #697199;
  border-radius: 5px;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.btn-add:hover {
  background-color: rgba(105, 113, 153, 0.2);
}

.select {
  background-color: rgba(105, 113, 153, 0.1);
  color: #697199;
  border: none;
}

a, li {
  text-decoration: none;
  list-style: none;
  margin: 0;
}

img {
  max-width: 100%;
}

button {
  border: none;
  margin: 0;
  padding: 0;
}
button:hover {
  cursor: pointer;
}

.container-task__item {
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: start;
  margin-bottom: 10px;
  font-weight: 600;
}
.container-task select {
  border-radius: 5px;
  border: 1px solid #e1e1e1;
}
.container-task input, .container-task textarea, .container-task select {
  min-width: 95%;
  margin: 0 5px;
}

.container-auth {
  padding: 10px;
}
@media (min-width: 768px) {
  .container-auth {
    padding: 30px;
  }
}
.container-auth__image {
  width: 200px;
}
@media (min-width: 768px) {
  .container-auth__image {
    width: 180px;
  }
}
.container-auth__pre-form {
  height: 100%;
  width: 55rem;
}
@media (min-width: 768px) {
  .container-auth__pre-form {
    height: auto;
  }
}
.container-auth__pre-form::before {
  position: absolute;
  content: "";
  width: 200px;
  height: 200px;
  border-radius: 50%;
  left: 0;
  background: rgb(13, 109, 252);
  background: linear-gradient(260deg, rgba(225, 225, 225, 0.1) 0%, #0D6EFD 100%);
  animation: contenido 3s infinite ease-in-out;
}
@keyframes contenido {
  0% {
    top: 90px;
  }
  50% {
    top: 110px;
  }
  100% {
    top: 90px;
  }
}
@media (min-width: 768px) {
  @keyframes contenido {
    0% {
      top: 0px;
    }
    50% {
      top: 20px;
    }
    100% {
      top: 0px;
    }
  }
}
.container-auth__pre-form::after {
  position: absolute;
  content: "";
  width: 200px;
  height: 200px;
  border-radius: 50%;
  right: 0;
  background: rgb(13, 109, 252);
  background: linear-gradient(90deg, rgba(225, 225, 225, 0.1) 0%, #0D6EFD 100%);
  animation: contenido1 3s infinite ease-in-out;
}
@keyframes contenido1 {
  0% {
    bottom: 90px;
  }
  50% {
    bottom: 110px;
  }
  100% {
    bottom: 90px;
  }
}
@media (min-width: 768px) {
  @keyframes contenido1 {
    0% {
      bottom: 0px;
    }
    50% {
      bottom: 20px;
    }
    100% {
      bottom: 0px;
    }
  }
}
.container-auth__form {
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(50px);
  padding: 20px;
}
@media (min-width: 768px) {
  .container-auth__form {
    padding: 40px;
  }
}
.container-auth__btn-password {
  display: none;
  color: #aeaeae;
}
.container-auth__btn-password--mostrar {
  display: block;
}

.navbar-options {
  transition: all 0.2s ease;
}
.navbar-options__nav-options {
  border-bottom: 1px solid #e1e1e1;
}
.navbar-options__buttons-group {
  display: none;
}
@media (min-width: 1024px) {
  .navbar-options__buttons-group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 5px;
  }
}
.navbar-options__button {
  padding: 4px;
  border-radius: 5px;
  background-color: rgba(105, 113, 153, 0.1);
  color: #697199;
  transition: all 0.3s ease;
}
.navbar-options__button:hover {
  background-color: rgba(105, 113, 153, 0.2);
  color: #697199;
}
.navbar-options__dropdown {
  display: block;
}
@media (min-width: 1024px) {
  .navbar-options__dropdown {
    display: none;
  }
}

.grid {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media (min-width: 1024px) {
  .grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    min-width: 100%;
    gap: 20px;
  }
}
@media (max-width: 1385px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.card {
  transition: all 0.2s ease;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.03);
  max-width: 100%;
  transition: box-shadow;
  transition-duration: 0.3s;
  background-color: #f8f9fa;
  border: 1px solid #e1e1e1;
}
.card:hover {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}
.card__span-priority {
  height: 3px;
  width: 50px;
  border-radius: 5px;
}
.card__span-priority--low {
  background-color: #91BEB7;
}
.card__span-priority--medium {
  background-color: #F2CE06;
}
.card__span-priority--high {
  background-color: #FF0000;
}
.card__content {
  height: 65px;
}
.card__title {
  color: #6C6C6C;
}
.card__description {
  color: #878787;
  line-height: 1.3;
  width: 90%;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.card__button-state--pending {
  color: #FF6A18;
  background-color: rgba(255, 106, 24, 0.2);
  transition-property: background-color;
  transition-duration: 0.3s;
  padding: 7px;
}
.card__button-state--pending:hover {
  background-color: rgba(255, 106, 24, 0.25);
}
.card__button-state--complete {
  color: #91BEB7;
  background-color: rgba(145, 190, 183, 0.2);
  transition-property: background-color;
  transition-duration: 0.3s;
  padding: 7px;
}
.card__button-state--complete:hover {
  background-color: rgba(145, 190, 183, 0.25);
}
.card__button-view {
  color: #697199;
  background-color: rgba(105, 113, 153, 0.2);
  transition-property: background-color;
  transition-duration: 0.3s;
  padding: 7px;
  padding: 6.5px;
}
.card__button-view:hover {
  background-color: rgba(105, 113, 153, 0.25);
}

.button-actions {
  padding: 7px;
  background-color: transparent;
}
.button-actions--edite {
  transition-property: background-color;
  transition-duration: 0.3s;
}
.button-actions--edite:hover {
  background-color: #eeeeee;
}
.button-actions--delete {
  color: #FF0000;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.button-actions--delete:hover {
  background-color: rgba(255, 0, 0, 0.1);
}

.view-task__header {
  color: #878787;
}
.view-task__header span {
  color: #0D6EFD;
}
.view-task__title {
  color: #6c6c6c;
}
.view-task__info {
  color: #6c6c6c;
}
.view-task__description {
  color: #878787;
}
.view-task__span {
  color: #6c6c6c;
}
.view-task__span--low {
  color: #91BEB7;
}
.view-task__span--medium {
  color: #F2CE06;
}
.view-task__span--high {
  color: #FF6A18;
}
.view-task__span-state--pending {
  color: #FF6A18;
}
.view-task__span-state--complete {
  color: #91BEB7;
}

.card-project {
  width: 100%;
  height: auto;
  background-color: #f8f9fa;
  transition-property: box-shadow;
  transition-duration: 0.3s;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
}
.card-project:hover {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}
.card-project__header {
  color: #6C6C6C;
  transition-property: color;
  transition-duration: 0.3s;
}
.card-project__header:hover {
  color: rgba(13, 110, 253, 0.6);
}
.card-project__date {
  color: #b4b4b4;
  font-weight: 500;
}
.card-project__count-task {
  background-color: rgba(105, 113, 153, 0.1);
  color: #697199;
}
.card-project__btn-actions {
  background-color: transparent;
}
.card-project__btn-actions--edite {
  transition-property: background-color;
  transition-duration: 0.3s;
  background-color: rgba(13, 110, 253, 0.1);
  color: #0D6EFD;
}
.card-project__btn-actions--edite:hover {
  background-color: rgba(13, 110, 253, 0.15);
}
.card-project__btn-actions--delete {
  transition-property: background-color;
  transition-duration: 0.3s;
  background-color: rgba(255, 0, 0, 0.1);
  color: #FF0000;
}
.card-project__btn-actions--delete:hover {
  background-color: rgba(255, 0, 0, 0.15);
}

.counter {
  color: #697199;
}

.task {
  transition-property: background-color;
  transition-duration: 0.3s;
  background-color: #F8F9FA;
  cursor: pointer;
}
.task:hover {
  background-color: #f2f4f6;
}
.task__info {
  width: 85%;
}
.task__title {
  color: #6C6C6C;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
.task__date {
  color: #878787;
}
.task__button-state--pending {
  color: #FF6A18;
  background-color: rgba(255, 106, 24, 0.2);
  transition-property: background-color;
  transition-duration: 0.3s;
  padding: 7px;
}
.task__button-state--pending:hover {
  background-color: rgba(255, 106, 24, 0.25);
}
.task__button-state--completed {
  color: #91BEB7;
  background-color: rgba(145, 190, 183, 0.2);
  transition-property: background-color;
  transition-duration: 0.3s;
  padding: 7px;
}
.task__button-state--completed:hover {
  background-color: rgba(145, 190, 183, 0.25);
}
.task__footer {
  width: 50%;
}

